.header-container {
  padding: 2rem;
  border-style: solid;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .home-button {
    display: flex;
    align-items: center;
  }
}

.logo-size {
  width: 3.125rem; /* Set the width as desired */
  height: auto; /* Maintain aspect ratio */
}

.logo-font-style {
  font-size: 100px;
}

.menu-items {
  font-size: 1rem;
  font-family: "Alegraya SC";

  a {
    padding-right: 1rem;
    padding-left: 1rem;
    text-transform: uppercase;
  }
}
